  public CreateGuid() {
    var d = new Date().getTime();
    var uuid = 'xxxxxxxxxxxx4xxxyxxxxxxxxxxxxxxx'.replace(/[xy]/g, (
      function (c) {
        var r = (d + Math.random() * 16) % 16 | 0;
        d = Math.floor(d / 16);
        return (c == 'x' ? r : (r & 0x3 | 0x8)).toString(16);
      }));
    return uuid.toUpperCase();
  }
  // è½¬æ¢ä¸ºsql..................
  public CaseToSql(col) {
    if (col == null || col == "")
      return ("åæ°ä¸ºç©º");

    let parameter = col.split(",");
    if (parameter.length == 0 || parameter.length == 1) {
      return ("å½æ°CaseToSql,åæ°ä¸ªæ°ä¸æ­£ç¡®");
    }

    let resultSql = " case " + parameter[0];

    let temp = "";
    //String[] valueStr = new String[2];
    for (let i = 1; i <= parameter.length; i++) {
      temp = parameter[i].replace("{", "");
      temp = temp.replace("}", "");

      let valueStr = temp.split(",");
      resultSql = resultSql + " when " + valueStr[0] + " then " + valueStr[1];
    }
    resultSql = resultSql + "end ";
    return resultSql;
  }
