  // è½¬ä¸ºä¸­æå¤§åéé¢
  public ToChineseMoney(money) {
    //æ±å­çæ°å­
    var cnNums = new Array('é¶', 'å£¹', 'è´°', 'å', 'è', 'ä¼', 'é', 'æ', 'æ', 'ç');
    //åºæ¬åä½
    var cnIntRadice = new Array('', 'æ¾', 'ä½°', 'ä»');
    //å¯¹åºæ´æ°é¨åæ©å±åä½
    var cnIntUnits = new Array('', 'ä¸', 'äº¿', 'å');
    //å¯¹åºå°æ°é¨ååä½
    var cnDecUnits = new Array('è§', 'å', 'æ¯«', 'å');
    //æ´æ°éé¢æ¶åé¢è·çå­ç¬¦
    var cnInteger = 'æ´';
    //æ´åå®ä»¥åçåä½
    var cnIntLast = 'å';
    //æå¤§å¤ççæ°å­
    var maxNum = 999999999999999.9999;
    //éé¢æ´æ°é¨å
    var integerNum;
    //éé¢å°æ°é¨å
    var decimalNum;
    //è¾åºçä¸­æéé¢å­ç¬¦ä¸²
    var chineseStr = '';
    //åç¦»éé¢åç¨çæ°ç»ï¼é¢å®ä¹
    var parts;
    if (money >= maxNum) {
      //è¶åºæå¤§å¤çæ°å­
      return '';
    }
    if (money == 0) {
      chineseStr = cnNums[0] + cnIntLast + cnInteger;
      return chineseStr;
    }
    //è½¬æ¢ä¸ºå­ç¬¦ä¸²

    var moneyStr = money.toString();
    if (moneyStr.indexOf('.') == -1) {
      integerNum = moneyStr;
      decimalNum = '';
    }
    else {
      parts = moneyStr.split('.');
      integerNum = parts[0];
      decimalNum = parts[1].substr(0, 4);
    }
    //è·åæ´åé¨åè½¬æ¢
    if (parseInt(integerNum, 10) > 0) {
      var zeroCount = 0;
      var IntLen = integerNum.length;
      for (var i = 0; i < IntLen; i++) {
        var n = integerNum.substr(i, 1);
        var p = IntLen - i - 1;
        var q = p / 4;
        var m = p % 4;
        if (n == '0') {
          zeroCount++;
        }
        else {
          if (zeroCount > 0) {
            chineseStr += cnNums[0];
          }
          //å½é¶
          zeroCount = 0;
          chineseStr += cnNums[parseInt(n)] + cnIntRadice[m];
        }
        if (m == 0 && zeroCount < 4) {
          chineseStr += cnIntUnits[q];
        }
      }
      chineseStr += cnIntLast;
    }
    //å°æ°é¨å
    if (decimalNum != '') {
      var decLen = decimalNum.length;
      for (var i = 0; i < decLen; i++) {
        var n = decimalNum.substr(i, 1);
        if (n != '0') {
          chineseStr += cnNums[Number(n)] + cnDecUnits[i];
        }
      }
    }
    if (chineseStr == '') {
      chineseStr += cnNums[0] + cnIntLast + cnInteger;
    }
    else if (decimalNum == '') {
      chineseStr += cnInteger;
    }
    return chineseStr;
  }
