  //å»é¤å­ç¬¦ä¸²ä¸­è¢«å­ç¬¦cåéçé¦å­ç¬¦ä¸²
  public trimStart(s, c) {
    if (s == null || s == "" || s.indexOf(c) < 0) {
      return s;
    }

    let startPos = s.indexOf(c);
    s = s.substring(startPos + 1);
    return s;
  }
  //å»é¤å­ç¬¦ä¸²ä¸­è¢«å­ç¬¦cåéçå°¾å­ç¬¦ä¸²
  public trimEnd(s, c) {
    if (s == null || s == "" || s.indexOf(c) < 0) {
      return s;
    }

    let lastPos = s.lastIndexOf(c);
    if (lastPos >= 0) {
      s = s.substring(0, lastPos);
    }
    return s;
  }
  //å»é¤å­ç¬¦ä¸²ä¸­è¢«å­ç¬¦cåéçé¦ãå°¾å­ç¬¦ä¸²
  public trimStartEnd(s, c) {
    return this.trimEnd(this.trimStart(s, c), c);
  }
