import { IExpressionContext } from "../core/index";

export class ExpressionContext implements IExpressionContext {
  private _arguments: { [prop: string]: any };
  private _contexts: { [prop: string]: any };
  public get contexts() {
    return this._contexts;
  }
  public get arguments() {
    return this._arguments;
  }
  constructor() {
    this._contexts = {};
  }
  public set(key: string, value: any): void {
    this._contexts[key] = value;
  }
  public setArgument(key: string, value: any) {
    this._arguments[key] = value;
  }
  public clear() {
    this._arguments = {};
  }
}